import React, { useState, useEffect } from "react";
import "./table.css";
const Table = ({ balance, setBalance, coins }) => {
  const [quantity, setquantity] = useState(0);
  const [totalvalue, settotalvalue] = useState(0);

  return (
    <div>
      <form action="">
        <div>please enter quantity of coin</div>
        <input
          type="number"
          name=""
          id=""
          onChange={(e) => {
            setquantity(e.target.value);
          }}
        />
      </form>
      <table>
        <th>Name</th>
        <th>symbol</th>
        <th>price</th>
        {coins.map((coin) => (
          <tr key={coin.id}>
            <button
              onClick={() =>
                settotalvalue(coin.metrics.market_data.price_usd * quantity)
              }
            >
              {coin.slug}
            </button>

            <td>{coin.symbol}</td>
            <td>{coin.metrics.market_data.price_usd.toFixed(2)}</td>
          </tr>
        ))}
      </table>

      <div>total cart value {totalvalue} </div>
      <button
        onClick={() => {
          {
            if (totalvalue > 1000) {
              alert("transaction declined not enough balance");
            } else {
              setBalance(balance - totalvalue);
              alert("transaction successful");
            }
          }
        }}
      >
        Buy Now
      </button>
    </div>
  );
};

export default Table;
